import { defineStore } from "pinia";
import axios from "axios";
import { config } from "./config";
import { useI18n } from "vue-i18n";

export const profileStore = defineStore("profile", {
  state: () => ({
    balance: 40000,
    speed: 0.36,
    chatId: "",
    name: "",
    process: {
      status: null, // active, closed
      remainingSeconds: 15,
    }
  }),

  actions: {
    // getUserProfile получает информацию о профиле пользователя
    async getUserProfile(queryForValidation: any) {
      const { locale } = useI18n(); // Вызов внутри метода

      try {
        const response = await axios.post(
          `${config.backendURL}/api/profile/myProfile`, queryForValidation
        );

        if (response.status !== 200) {
          throw new Error(
            "Не удалось создать обращение. Статус ответа от сервера не 200: " +
              response.status
          );
        }

        const process = response.data.process;

        this.process.status = process.status;

        if (this.process.status === "ACTIVE") {
          this.process.remainingSeconds = process.remainingSeconds
        }

        this.balance = response.data.balance;
        this.speed = response.data.summPower;
        this.chatId = response.data.chatId;
        this.name = response.data.name;
        locale.value = response.data.language || "en";
      } catch (error) {
        console.error("Ошибка при запросе профиля пользователя:", error);
        throw error;
      }
    },

    // claimProcessReward отправляет запрос для сбора наград по процессу
    async claimProcessReward(queryForValidation: any) {
      try {
        const response = await axios.post(
            `${config.backendURL}/api/profile/myProfile`, queryForValidation
        );

        if (response.status !== 200) {
          throw new Error(
              "Не удалось создать обращение. Статус ответа от сервера не 200: " +
              response.status
          );
        }

        this.process.status = null;

      } catch (error) {
        console.error("Ошибка при запросе сбора наград:", error);
        throw error;
      }
    },

    // startProcess отправляет запрос для начала нового процесса сбора
    async startProcess(queryForValidation: any) {
      try {
        const response = await axios.post(
            `${config.backendURL}/api/profile/myProfile`, queryForValidation
        );

        if (response.status !== 200) {
          throw new Error(
              "Не удалось создать обращение. Статус ответа от сервера не 200: " +
              response.status
          );
        }

        const process = response.data.process;

        this.process.status = process.status;
        this.process.remainingSeconds = process.remainingSeconds;
      } catch (error) {
        console.error("Ошибка при запросе начала сбора:", error);
        throw error;
      }
    }
  },

  getters: {
    getBalance(state) {
      return state.balance;
    },
    getSpeed(state) {
      return state.speed.toFixed(2);
    },
    getRemainingTime(state) {
      return state.process.remainingSeconds;
    },
    getStatus(state) {
      return state.process.status;
    },
  },
});
